.mainImgContainer {
  position: relative;
}

.mainImg {
  z-index: -1;
  width: 100%;
  min-width: 1080px;
}

.mainImgText {
  font-family: "Raleway", sans-serif;
  width: 100%;
  font-weight: 700;
  z-index: 1;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  font-size: 64px;
  z-index: 0;
  color: whitesmoke;
  cursor: default;
}
